import { StrictMode } from 'react';
import { createRoot } from 'react-dom/client';
import App from './App.tsx';
import './index.css';
import './App.css';
import { registerServiceWorker } from './lib/serviceWorker.ts';
import { preloadCriticalImages } from './lib/imageOptimizer.ts';

// Registrar Service Worker para cache offline e melhor performance
registerServiceWorker();

// Pr√©-carregar imagens cr√≠ticas
preloadCriticalImages([
  // Adicione aqui URLs de imagens cr√≠ticas que devem ser pr√©-carregadas
  // Exemplo: logos, backgrounds de login, etc.
]);

// Adicionar meta tags para PWA
if (typeof document !== 'undefined') {
  // Theme color
  const metaThemeColor = document.createElement('meta');
  metaThemeColor.name = 'theme-color';
  metaThemeColor.content = '#3B82F6';
  document.head.appendChild(metaThemeColor);
  
  // Apple touch icon
  const appleTouchIcon = document.createElement('link');
  appleTouchIcon.rel = 'apple-touch-icon';
  appleTouchIcon.href = '/icons/apple-touch-icon.png';
  document.head.appendChild(appleTouchIcon);
  
  // Manifest
  const manifestLink = document.createElement('link');
  manifestLink.rel = 'manifest';
  manifestLink.href = '/manifest.json';
  document.head.appendChild(manifestLink);
}

// Definir vari√°vel global para socket
window.socket = null;

createRoot(document.getElementById('root')!).render(
  <StrictMode>
    <App />
  </StrictMode>
);